import React from 'react';
import './Body.scss';
import BannerLogo from './images/banner-logo.png';
import ReadyMadeTopLeftLogo from './images/ready-made-top-left-logo.png';
import ReadyMadeTopRightLogo from './images/ready-made-top-right-logo.png';
import ReadyMadeBottomLeftLogo from './images/ready-made-bottom-left-logo.png';
import ReadyMadeBottomRightLogo from './images/ready-made-bottom-right-logo.png';
import BuildIcon from './images/build-ico.png';
import GlassIcon from './images/glass-ico.png';
import YearIcon from './images/2year-ico.png';
import Slider from "react-slick";


export default function Body() {
    var settings = {
        autoplay: true,
        autoplaySpeed: 1500,
        dots: true,
        infinite: true,
        draggable: true,
        slidesToShow: 3,
        slidesToScroll: 1,
        initialSlide: 0,
        centerMode: true,
        // variableWidth: true,
        responsive: [
            {
                breakpoint: 1365,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 1,
                    infinite: true,
                    dots: true
                }
            },
            {
                breakpoint: 767,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1,
                    initialSlide: 2
                }
            },
            {
                breakpoint: 480,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1
                }
            }
        ]
    };

    return (
        <div className="container-fluid">
            <div className="row banner">
                <div className="container">
                    <div className="content-wrapper">
                        <div className="logo-image">
                            <img src={BannerLogo} alt="GRIGS" className="responsive-img" />
                        </div>
                        <div className="logo-text-wrapper">
                            <h1>GAMING PCs MADE SIMPLE</h1>
                            <h3>Choose a game, set a budget, and we'll BLD it</h3>
                            <a href="/bld" className="btn get-started">GET STARTED</a>
                        </div>
                    </div>
                </div>
            </div>

            <div className="row ready-made">
                <div className="col-md-6">
                    <section className="ready-made-section top-left">
                        <div className="ready-made-section-content">
                            <h3 className="ready-made-section-title">$899 Starter PC</h3>
                            <p className="ready-made-section-sub">Welcome to better gaming</p>
                            <button className="ready-made-section-button btn btn-purple">Buy Now</button>
                        </div>
                        <div className="ready-made-section-image">
                            <img src={ReadyMadeTopLeftLogo} alt="$899 Starter PC Build" />
                        </div>
                    </section>
                </div>

                <div className="col-md-6">
                    <section className="ready-made-section top-right">
                        <div className="ready-made-section-content">
                            <h3 className="ready-made-section-title">$999 Starter PC Plus</h3>
                            <p className="ready-made-section-sub">Take your game further</p>
                            <button className="ready-made-section-button btn btn-purple">Buy Now</button>
                        </div>
                        <div className="ready-made-section-image">
                            <img src={ReadyMadeTopRightLogo} alt="$999 Starter PC Plus Build" />
                        </div>
                    </section>
                </div>

                <div className="col-md-6">
                    <section className="ready-made-section bottom-left">
                        <div className="ready-made-section-content">
                            <h3 className="ready-made-section-title">$1499 Streaming PC</h3>
                            <p className="ready-made-section-sub">Don't let your streams be dreams</p>
                            <button className="ready-made-section-button btn btn-purple">Buy Now</button>
                        </div>
                        <div className="ready-made-section-image">
                            <img src={ReadyMadeBottomLeftLogo} alt="$1499 Streaming PC Build" />
                        </div>
                    </section>
                </div>

                <div className="col-md-6">
                    <section className="ready-made-section bottom-right">
                        <div className="ready-made-section-content">
                            <h3 className="ready-made-section-title">$1999 H1 Mini PC</h3>
                            <p className="ready-made-section-sub">Game big, go Mini</p>
                            <button className="ready-made-section-button btn btn-purple">Buy Now</button>
                        </div>
                        <div className="ready-made-section-image">
                            <img src={ReadyMadeBottomRightLogo} alt="$1999 H1 Mini PC Build" />
                        </div>
                    </section>
                </div>
            </div>

            <div className="row service-package">
                <div className="col-12 p-a-0 m-y">
                    <Slider {...settings}>
                        <div className="service-details col-12 col-md-4 text-center m-y">
                            <div data-v-cfa0248e="" className="img-container">
                                <img data-v-cfa0248e="" src={BuildIcon} alt="" className="m-auto" />
                            </div>
                            <div data-v-cfa0248e="" className="content">
                                <div data-v-cfa0248e="" className="text-white text-upper">
                                    <h3 data-v-cfa0248e="" className="text-bold">OPTIMIZED FOR GAME <br data-v-cfa0248e="" /> PERFORMANCE</h3>
                                </div>
                                <div data-v-cfa0248e="" className="text text-white">
                                    <p data-v-cfa0248e="" className="text-bold">We’ve done the research and our algorithm takes care of the rest.</p>
                                </div>
                            </div>
                            <a data-v-cfa0248e="" href="/what-is-bld" className="link-btn link-white" tabIndex="0">LEARN MORE<i data-v-cfa0248e="" aria-hidden="true" className="fa fa-chevron-right"></i></a>
                        </div>

                        <div className="service-details col-12 col-md-4 text-center m-y">
                            <div data-v-cfa0248e="" className="img-container">
                                <img data-v-cfa0248e="" src={GlassIcon} alt="" className="m-auto" />
                            </div>
                            <div data-v-cfa0248e="" className="content">
                                <div data-v-cfa0248e="" className="text-white text-upper">
                                    <h3 data-v-cfa0248e="" className="text-bold">CLEAR, COMPETITIVE<br data-v-cfa0248e="" />PRICING</h3>
                                </div>
                                <div data-v-cfa0248e="" className="text text-white">
                                    <p data-v-cfa0248e="" className="text-bold">Customize your build with our curated parts with straight forward pricing and a flat $99 assembly fee.</p>
                                </div>
                            </div>
                            <a data-v-cfa0248e="" href="/what-is-bld" className="link-btn link-white" tabIndex="0">LEARN MORE<i data-v-cfa0248e="" aria-hidden="true" className="fa fa-chevron-right"></i></a>
                        </div>

                        <div className="service-details col-12 col-md-4 text-center m-y">
                            <div data-v-cfa0248e="" className="img-container">
                                <img data-v-cfa0248e="" src={YearIcon} alt="" className="m-auto" />
                            </div>
                            <div data-v-cfa0248e="" className="content">
                                <div data-v-cfa0248e="" className="text-white text-upper">
                                    <h3 data-v-cfa0248e="" className="text-bold">WARRANTY ON ALL<br data-v-cfa0248e="" />PARTS AND LABOR</h3>
                                </div>
                                <div data-v-cfa0248e="" className="text text-white">All components under one warranty plan – NZXT will manage any problems for you.</div>
                            </div>
                            <a data-v-cfa0248e="" href="/what-is-bld" className="link-btn link-white" tabIndex="0">LEARN MORE<i data-v-cfa0248e="" aria-hidden="true" className="fa fa-chevron-right"></i></a>
                        </div>
                    </Slider>
                </div>
            </div>
        </div>
    );
}